button{
    width: 250px;
    height: 80px;
    font-size: 35px;
    font-family: 'Roboto', sans-serif;
    font-weight: bold;
    color: #fff;
    border: 2px solid white;
    border-radius: 15px;margin: 5px;
    cursor: pointer;
    &:hover{
        background-color: #00471b;
    }
}
.click-button{
    background-color: #002ead;
}

.reset-button{
    background-color: #5a01a7;
}